<template>
  <div id="app">
    <v-app v-cloak>
      <v-app-bar
        app
        class="secondary"
      >
        <v-toolbar-title>
          <v-icon class="pr-2">mdi-bank-outline</v-icon>
          SG Bank Interests Compare
        </v-toolbar-title>

        <v-spacer></v-spacer>

        <span><v-icon>mdi-weather-night</v-icon></span>
        <v-switch
          hide-details
          v-model="theme_toggle"
          @click="switchTheme()"
        >
        </v-switch>
        <span><v-icon>mdi-weather-sunny</v-icon></span>
      </v-app-bar>

      <v-main
        class="primary"
      >

        <v-container
          fluid
        >
          <calculator-form v-model="disclaimer"></calculator-form>

        </v-container>

        <v-dialog
          v-model="disclaimer"
          persistent
          scrollable
        >
          <v-card
            class="warning"
          >
            <v-card-text
              class="pa-4"
            >
              <p><strong>Disclaimer:</strong> This site does not provide financial advice nor does it endorse any of the financial institutions referenced. The interests calculation is only an approximation and may not be 100% accurate. Please always refer to the official website for calculation. Comparison for reference only and should not be used as the sole reason for making certain financial decisions. The owner of this site shall not be responsible for any financial loss of its users.</p>
              <p>The interest rates are correct as of 2024-02-02.</p>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>

              <v-btn
                text
                @click="disclaimer = false"
              >
                I understand
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

      </v-main>

      <v-footer
        app
        padless
        fixed
      >
        <v-card
          flat
          tile
          width="100%"
          class="secondary pa-1 text-center"
        >
          <v-card-subtitle
            class="info py-1 text-h7"
          >
            <v-icon
              x-small
            >
              mdi-monitor
            </v-icon>
            This site is best viewed on screen size bigger than 800x600.
          </v-card-subtitle>

          <v-divider></v-divider>

          <v-card-text
            class="py-1 text-center"
          >
            Coffee Brew Apps © {{ new Date().getFullYear() }} — <strong>SG Bank Interests Compare</strong>
            <v-btn
              v-for="icon in icons"
              :key="icon.icon"
              class="ma-0"
              icon
              @click="openLink(icon.url)"
            >
              <v-tooltip
                right
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-icon
                    size="20px"
                    v-bind="attrs"
                    v-on="on"
                  >
                    {{ icon.icon }}
                  </v-icon>
                </template>
                <span>{{ icon.tooltip }}</span>
              </v-tooltip>
            </v-btn>
          </v-card-text>
        </v-card>
      </v-footer>

    </v-app>
  </div>
</template>

<script>
import CalculatorForm from './components/CalculatorForm';

export default {
  name: 'App',

  components: {
    CalculatorForm
  },

  created() {
  },

  data: () => ({
    disclaimer: true,
    theme_toggle: true,
    icons: [
      {
        icon: 'mdi-github',
        tooltip: 'Report Issues',
        url: 'https://github.com/coffeebrewapps/sg_bank_int_compare'
      }
    ]
  }),

  computed: {
    theme_name: function() {
      let current_theme = this.theme_toggle;
      if (current_theme) {
        return 'dark';
      } else {
        return 'light';
      }
    }
  },

  methods: {
    openLink: function(url) {
      window.open(url);
    },

    switchTheme: function() {
      // true - dark, false - light
      let current_theme = this.theme_toggle;
      if (current_theme) {
        this.$vuetify.theme.dark = false;
      } else {
        this.$vuetify.theme.dark = true;
      }
    },
  },

  watch: {
  },

};

</script>

